@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
:root {
  --body-bg: #f5f5f5;
  --txt-color: #6d6d6d;
  --txt-white: #fff;
  --calc-res-color: #2f2d2f;
  --calc-btn-color: rgb(233, 48, 233);
  --calc-bg: #fff;
}
.dark {
  --calc-res-color: #fff;
  --calc-bg: #373737;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
body {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  background-color: #f5f5f5;
  background-color: var(--body-bg);
  color: #6d6d6d;
  color: var(--txt-color);

  display: flex;
  justify-content: center;
  padding-top: 100px;
}
.button {
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  body {
    display: unset;
    padding-top: 0;
  }
}

.calculator {
  padding: 20px;
  height: 700px;
  width: 400px;
  overflow: hidden;
  border-radius: 20px;
  background-color: var(--calc-bg);
  box-shadow: rgba(100, 100, 110, 0.2) 0px 7px 29px 0px;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.calcu_btns {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  gap: 10px;
}

.calcu_btns button {
  border: 1px solid black;
  outline: 0;
  border-radius: 10px;
  font-size: 1.25rem;
  font-weight: bolder;
  background-color: transparent;
  color: var(--txt-color);
  transition:all 1s
}
.calcu_btns button:hover {
  background-color: grey;
  opacity: 0.6;
  color: black;
}

.calcu_btns button:last-child {
  border-radius: 50%;
  color: var(--txt-white);
  background-image: linear-gradient( #cd6e36, indigo);
  border: none;
}
.calcu_btns button:hover:last-child {
  color: black;
}
.calcu_btns .btn__op {
  color: var(--calc-btn-color);
  border: 1px solid var(--calc-btn-color);
}
.calcu_btns .btn__op:hover {
  background-color: var(--calc-btn-color);
  color: white;
}

.calcu_result {
  position: relative;
  margin-bottom: 20px;
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
}

.calcu_result_exp {
  font-size: 3.5rem;
  line-height: 1;
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  color: var(--calc-res-color);
}
.calcu_result_exp:last-child {
  position: absolute;
  right: 0;
}
.calcu_result_exp span {
  display: inline-block;
  overflow: hidden;
  transition: width 0.3s ease;
}

@media only screen and(max-width:800px) {
  .calculator {
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }
}

